.container {
  width: 100%;
  padding: 0 10px;
}

@media screen and (min-width: 320px) {
  .container {
    margin: 0 auto;
  }
}

@media screen and (min-width: 768px) {
  .container {
    display: flex;
  }
}
@media screen and (min-width: 1200px) {
  .container {
    padding: 0 15px;
  }
}
